import React, {useEffect} from 'react';
import './timeline.css';

const Timeline = () => {
    useEffect(() => {
        const handleScroll = () => {
            const boxes = document.querySelectorAll(".box");
            const triggerBottom = (window.innerHeight / 5) * 4;

            boxes.forEach((box) => {
                const topBox = box.getBoundingClientRect().top;
                if (topBox < triggerBottom) {
                    box.classList.add("show");
                } else {
                    box.classList.remove("show");
                }
            });
        };

        window.addEventListener("scroll", handleScroll);
        handleScroll();

        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);
    return (
        <section id="timeline" className="timeline">
            <i className="fa-solid fa-timeline"></i>
            <h2>tapes de Projet</h2>
            <ul>
                <li>
                    <i className="fa-solid fa-handshake"></i>
                    <div className="box">
                        <div className="shadow-card-timeline">
                            <h3 className="title-time">tape 1: Consultation Initiale</h3>
                            <p className="card-info">
                                Premire rencontre pour discuter de vos besoins. Identification des objectifs du projet et des contraintes techniques.
                            </p>
                        </div>
                    </div>
                </li>
                <li>
                    <i className="fa-solid fa-file-signature"></i>
                    <div className="box">
                        <div className="shadow-card-timeline">
                            <h3 className="title-time">tape 2: Proposition de Devis</h3>
                            <p className="card-info">
                                Prparation et prsentation d'un devis dtaill, incluant les fonctionnalits souhaites, le budget et le calendrier estim.
                            </p>
                        </div>
                    </div>
                </li>
                <li>
                    <i className="fa-solid fa-pencil-ruler"></i>
                    <div className="box">
                        <div className="shadow-card-timeline">
                            <h3 className="title-time">tape 3: Conception et Design</h3>
                            <p className="card-info">
                                Cration des maquettes et prototypes si non fournies. Validation des choix de design avant de passer au dveloppement.
                            </p>
                        </div>
                    </div>
                </li>
                <li>
                    <i className="fa-solid fa-code"></i>
                    <div className="box">
                        <div className="shadow-card-timeline">
                            <h3 className="title-time">tape 4: Dveloppement</h3>
                            <p className="card-info">
                                Dveloppement du site web selon les spcifications convenues. Tests itratifs pour assurer la qualit et la conformit aux attentes.
                            </p>
                        </div>
                    </div>
                </li>
                <li>
                    <i className="fa-solid fa-bug"></i>
                    <div className="box">
                        <div className="shadow-card-timeline">
                            <h3 className="title-time">tape 5: Test et Rvision</h3>
                            <p className="card-info">
                                Tests complets pour identifier et corriger les bugs. Rvision finale avec le client pour s'assurer que tout est conforme aux attentes.
                            </p>
                        </div>
                    </div>
                </li>
                <li>
                    <i className="fa-solid fa-rocket"></i>
                    <div className="box">
                        <div className="shadow-card-timeline">
                            <h3 className="title-time">tape 6: Lancement</h3>
                            <p className="card-info">
                                Mise en ligne du site web. Surveillance des performances initiales et ajustements si ncessaire. Transfert de la proprit au client.
                            </p>
                        </div>
                    </div>
                </li>
                <li>
                    <i className="fa-solid fa-tools"></i>
                    <div className="box">
                        <div className="shadow-card-timeline">
                            <h3 className="title-time">tape 7: Support et Maintenance</h3>
                            <p className="card-info">
                                Assistance selon l'offre pour la maintenance du site, mises  jour et amlioration des fonctionnalits.
                            </p>
                        </div>
                    </div>
                </li>
            </ul>
        </section>
    );
}

export default Timeline;